
export default {
   
    search: function(searchTerm) {
        
        return fetch(`https://api.fda.gov/drug/label.json?search=${searchTerm}+AND+openfda.product_type:prescription`)
        .then(res => res.json()) 
        .then(data => data.results.map(data =>data))
        .catch(err => console.log(err, "Cound not find the drug")); 
        
    },
    searchOtc: function(searchTermOtc) {
        return fetch(`https://api.fda.gov/drug/label.json?search=${searchTermOtc}+AND+openfda.product_type:otc`)
        .then(res => res.json()) 
        .then(data => data.results.map(data =>data))
        .catch(err => console.log(err, "Cound not find the drug")); 
        
    }
};
