const searchFormOtc = document.getElementById('search-form-otc')
import fda from './fda.api'

const searchInputOtc = document.getElementById('search-input-otc')

// form event listener
searchFormOtc.addEventListener('submit', e=> {

    // get search term
    const searchTermOtc = searchInputOtc.value;
    
    console.log(searchTermOtc)
    
    // check input 
    if(searchTermOtc === '') {
        showMessage('Please add a search term','alert-danger')
    }
    
    // clear input
    searchInputOtc.value = '';
 
    //Serach FDA
    fda.searchOtc(searchTermOtc)
        .then(results => {
            console.log(results)
            let output = '<div>';
            //loop thru meds                                                                                                                                               
            results.forEach(result => {
        
                    output += `
                     <div class="jumbotron">
                    <h3 class="display-4">${result.openfda.generic_name}</h3>
                    <p>Effective Time: ${result.effective_time}</p>
                    <p>Brand Name: ${result.openfda.brand_name}</p>
                    <p>${result.purpose}</p>
                    <p>${result.active_ingredient}</p>
                    <hr class="my-4">
                   
                    </div>
                `;
                    output+= '</div>';
                         
            })
                

            document.getElementById('resultsOtc').innerHTML = output;
        })
    e.preventDefault()
});

// Show message 
function showMessage(message, className) {
    // create div
    const div = document.createElement('div');
    // add class
    div.className =`alert ${className}`;
    // add text

    div.appendChild(document.createTextNode(message));
    // get parent element
    const searchContainer = document.getElementById('search-container');

    //get search
    const search = document.getElementById('search');

    //Insert Message
    searchContainer.insertBefore(div, search);

   // Time out set
   setTimeout(()=>document.querySelector('.alert').remove(), 3000);
}